.header-blog-newsletter {
  font-weight: 500;
  font-size: 18px;
  line-height: 1.22;
  text-align: center;
  color: $text-color;
  padding-bottom: 24px;
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    font-size: 32px;
    line-height: 1.219;
    padding-bottom: 32px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 32px;
    line-height: 1.219;
    padding-bottom: 32px;
  }
}
.header-title {
  display: flex;
}
.section__title {
  display: inline-flex;
  text-transform: uppercase;
}
.line {
  display: block;
  width: 100%;
  position: relative;
  bottom: 27px;
}

.slide-container {
  max-width: 1232px;
  width: 100%;
  padding: 122px 0 103px 0;
}
.slide-content {
  overflow: hidden;
}

.image-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  height: auto;
  width: 390px;
}
.image-content .card-img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.swiper-button-next {
  right: 0;
}
.swiper-button-prev {
  left: 0;
}

