.button {

    display: block;
    font-family: $font-primary;
    color: $primary-white-color;
    font-weight: 500;
    line-height: 24px;
    text-decoration: none;
    text-align: center;
    cursor: pointer;
    background-color: $primary-orange-color;
    border: none;
    border-radius: 38px;

  display: block;
  font-family: $font-primary;
  color: $primary-white-color;
  font-weight: 500;
  line-height: 24px;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  background-color: $primary-orange-color;
  border: none;
  border-radius: 38px;
  &:hover {
    background-color: $hover-orange-color;
  }
  &:focus {
    background-color: $focus-orange-color;
  }
  @media screen and (min-width: 320px) and (max-width: 767px) {
    width: 208px;
    padding: 10px 0;
    font-size: 20px;
    &--mobile-s {
      width: 170px;
      padding: 8px 0;
      font-size: 18px;
    }
  }
  @media screen and (min-width: 768px) {
    font-size: 28px;
    padding: 20px 0;
    width: 280px;
  }
  &--mobile-m {
    padding: 10px 35px;
    font-size: 20px;
  }
  &--desktop-tablet {
    font-size: 28px;
    padding: 20px 0;
    width: 280px;
  }
}

.button-popup {
